'use strict';

// MODULES //
var DTYPE_CHAR = __var_0__;


// MAIN //

/**
* Returns the single letter character abbreviation for an underlying array data type.
*
* @param {string} dtype - data type
* @returns {(string|null)} single letter character abbreviation
*
* @example
* var ch = dtypeChar( 'float64' );
* // returns 'd'
*
* ch = dtypeChar( 'generic' );
* // returns 'o'
*/
function dtypeChar( dtype ) {
	return DTYPE_CHAR[ dtype ] || null;
}


// EXPORTS //
export default dtypeChar;
